@font-face {
    font-family: 'FS Albert';
    src: url('./font/fs-bold-webfont.eot'); /* IE9 Compat Modes */
    src: url('./font/fs-bold-webfont.woff') format('woff'), /* Modern Browsers */
         url('./font/fs-bold-webfont.woff2') format('woff2'), /* Modern Browsers */
         url('./font/fs-bold-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
         url('./font/fs-bold-webfont.svg') format('svg'); /* Legacy iOS */
    font-weight: bold;
}

@font-face {
    font-family: 'FS Albert';
    src: url('./font/fs-regular-webfont.eot'); /* IE9 Compat Modes */
    src: url('./font/fs-regular-webfont.woff') format('woff'), /* Modern Browsers */
         url('./font/fs-regular-webfont.woff2') format('woff2'), /* Modern Browsers */
         url('./font/fs-regular-webfont.ttf')  format('truetype'), /* Safari, Android, iOS */
         url('./font/fs-regular-webfont.svg') format('svg'); /* Legacy iOS */
    font-weight: normal;
}
